.searchBar-wrap {
  background-color: #f0f0f0;
  width: fit-content;
  margin: 2.5rem auto 4rem auto;
  padding: 0.5rem;
  border-radius: 5px;
}

.searchBar-wrap form {
  display: flex;
  align-items: center;
}

.searchBar-wrap input {
  background-color: #f0f0f0;
  outline: none;
  border: none;
}

.searchBar-wrap span {
  /* padding: 0.5rem 1rem; */
  margin-right: 0.5rem;
  cursor: pointer;
  /* background: red; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchBar-wrap span svg {
  width: 20px;
  height: 20px;
}

.searchBar-wrap button {
  outline: none;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  background-color: #0f52ba;
  color: #fff;
  font-size: 1.1rem;
  cursor: pointer;
}
